<template>
  <div class="tag" v-if='showTags'>
    <div class="tags" :class="{active: collapse}">
      <el-tag
      class="el-tag"
      :class="{el_active: isActive(tag.path)}"
      :key="tag.path"
      v-for="tag in dynamicTags"
      closable
      :disable-transitions="false"
      @click="handleTag(tag)"
      @close="handleClose(tag)">
      {{tag.title}}
    </el-tag>
<!--     <el-input
      class="input-new-tag"
      v-if="inputVisible"
      v-model="inputValue"
      ref="saveTagInput"
      size="small"
    >
    </el-input> -->
  </div>
  </div>
</template>

<script>
import bus from './common/bus'
export default {
  name: 'Tag',
      data() {
      return {
        collapse: false,
        dynamicTags: [],
        inputVisible: false,
        inputValue: ''
      };
    },
    methods: {
      // 激活高亮
      isActive (path) {
        return this.$route.fullPath === path
      },
      //  关闭tag
      handleClose(tag) {
        const index = this.dynamicTags.indexOf(tag);
        const delItem = this.dynamicTags.splice(index, 1)[0];
        const item = this.dynamicTags[index] ? this.dynamicTags[index] : this.dynamicTags[index-1]
        if(item){
          delItem.path === this.$route.fullPath && this.$router.push(item.path)
        } else {
          delItem.path !== '/dashboard' &&  this.$router.push('/')
        }

        
        
      },
      //单击标签
      handleTag(tag) {
        if (tag.path !== this.$route.path){
          this.$router.push(tag.path)
        }
        
      },
      // 设置标签
      setTag (route) {
        const isExist = this.dynamicTags.some(item => {
          return item.path === route.fullPath
        })
        if(!isExist){
          if(this.dynamicTags.length >= 8 ){
            this.dynamicTags.shift()
          }
            this.dynamicTags.push({
            title: route.meta.title,
            path: route.fullPath,
            name: route.matched[1].components.default.name
          })
        }
        bus.$emit('tags', this.dynamicTags)
      }
    },
    watch:{
      $route(newValue, oldValue){
        this.setTag(newValue)
        console.log(oldValue);
        
      }
    },
    computed: {
      showTags () {
        return this.dynamicTags.length > 0
      }
    },   
    created() {
      this.setTag(this.$route)

      bus.$on('collapse', (msg)=> {
        this.collapse = msg;
      })
    }
}
</script>

<style scoped>
  .tag {
    position: relative;
    width: 100%;
    background: #fff;
    height: 37px;
    line-height: 37px;
    box-shadow: 0 5px 10px #ddd;
    padding-right:120px ;
  }
  .el-tag {
    cursor: pointer;
    color: #666;
    background:(0,0,0,0.1);
  }
  .tags .el_active {
    color: #fff;
    background: rgb(67, 135, 212);
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
</style>