<template>
  <div class="sidebar">
      <el-menu
        :default-active="onRoutes"
        class="el-menu-vertical-demo"
        :collapse='collapse'
        background-color="#324157"
        text-color="#bfcbd9"
        active-text-color="#20a0ff"
        unique-opened
        router
        @select='handleMenu'
        >
        <template v-for="item in items">
          <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index" >
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{item.title}}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu 
                v-if="subItem.subs"
                :index="subItem.index"
                :key="subItem.index"
              >
                <template slot="title">{{subItem.title}}</template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >{{threeItem.title}}
                </el-menu-item>
                </el-submenu>
                <el-menu-item
                  v-else
                  :index="subItem.index"
                  :key="subItem.index"
                >
                {{subItem.title}}
                </el-menu-item>
            </template>
          </el-submenu>
          </template>
          <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
          </template>
        </template>
      </el-menu>
    </div>
</template>

<script>
import bus from './common/bus'
// import { log } from 'util'
export default {
  name: 'Home',
  data () {
    return {
            collapse: false,
            items: [
                {
                    icon: 'el-icon-house',
                    index: 'dashboard',
                    title: '系统首页'
                },
                {
                    icon: 'el-icon-s-order',
                    index: 'table',
                    title: '基础表格'
                },
                {
                    icon: 'el-icon-more',
                    index: 'tabs',
                    title: 'tab选项卡'
                },
                {
                    icon: 'el-icon-tickets',
                    index: '3',
                    title: '表单相关',
                    subs: [
                        {
                            index: 'form',
                            title: '基本表单'
                        },
                        {
                            index: '3-2',
                            title: '三级菜单',
                            subs: [
                                {
                                    index: 'editor',
                                    title: '富文本编辑器'
                                },
                                {
                                    index: 'markdown',
                                    title: 'markdown编辑器'
                                }
                            ]
                        },
                        {
                            index: 'upload',
                            title: '文件上传'
                        }
                    ]
                },
                {
                    icon: 'el-icon-coordinate',
                    index: 'icon',
                    title: '自定义图标'
                },
                {
                    icon: 'el-icon-pie-chart',
                    index: 'charts',
                    title: 'schart图表'
                },
                {
                    icon: 'el-icon-rank',
                    index: '6',
                    title: '拖拽组件',
                    subs: [
                        {
                            index: 'drag',
                            title: '拖拽列表'
                        },
                        {
                            index: 'dialog',
                            title: '拖拽弹框'
                        }
                    ]
                },
                {
                    icon: 'el-icon-reading',
                    index: 'i18n',
                    title: '国际化功能'
                },
                {
                    icon: 'el-icon-folder-delete',
                    index: '7',
                    title: '错误处理',
                    subs: [
                        {
                            index: 'permission',
                            title: '权限测试'
                        },
                        {
                            index: '404',
                            title: '404页面'
                        }
                    ]
                },
                {
                    icon: 'el-icon-document-checked',
                    index: 'donate',
                    title: '支持作者'
                }
            ]
        }
  },
  methods: {
    handleMenu (title) {
      bus.$emit('active-route',title)
    }
  },
  computed: {
    onRoutes() {
      
      return this.$route.path.replace('/','')
    },
  },

    created() {
        bus.$on('collapse', (msg)=> {
          this.collapse = msg;
          bus.$emit('collapse-content', msg)
        })

        
      }
}
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
  z-index: 10;
}
.sidebar::-webkit-scrollbar {
    width: 0;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 250px;
}
.sidebar > ul {
  height: 100%;
}
</style>